.summary {
    padding: 15px 20px;

    .section {
        margin-bottom: 1em;
    }

    .work {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        align-items: flex-start;

        .buttons {
            display: flex;
            flex-direction: column;
            align-items: flex-end;

            button {
                margin: 0;
                margin-bottom: .2em;
            }
        }
    }

    .export-section {
        margin-top: 40px;
    }
    
    .export-inputs {
        display: flex;
        flex-direction: row;
        align-items: center;
    }
    
    .export-inputs *,
    .export-inputs input {
        margin: 0;
        margin-right: 6px;
    }

    .deploy-info {
        position: absolute;
        font-size: 10px;
        bottom: .4em;
        right: .8em;
    }
}
