.work-button-wrapper {
    display: flex;
    flex-direction: row;
}

.work-button {
    min-width: 87;
    border-bottom-right-radius: 0;
    border-top-right-radius: 0;
    margin: 0;
}

.cancel-work-button {
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
    margin: 0;
}
